import AppDispatcher from '../dispatcher/dispatcher';
import EventEmitter from 'events';
import resources from '../utils/resources.json'

const CHANGE_EVENT = 'change';

let _nodes = resources;

class BuildingStoreClass extends EventEmitter {

    emitChange(resource) {
        this.emit(resource);
    }

    addChangeListener(callback) {
        this.on(CHANGE_EVENT, callback);
    }

    get(id) {
        return _nodes[id];
    }

    getAll() {
        return _nodes;
    }
};

let BuildingStore = new BuildingStoreClass();

BuildingStore.dispatchToken = AppDispatcher.register(function(payload) {
    const action = payload.action;

    switch(action.type) {

        case "UPDATE_BUILDING":
        _nodes[action.resource] = action.value;
        BuildingStore.emitChange(action.resource);
        break;

        default:
        // do nothing
    }

});

export default BuildingStore


