import React from 'react';

import Buildings from '../../utils/buildings.json'
import { updateBuilding } from '../../actions/buildingAction'

export default class Building extends React.Component {
    refs = {}

    constructor(props) {
        super(props)

        this.state = {
            details: ''
        }
    }

    addRef = ref => {
        this.refs = ref;
    }

    purchaseBuilding = () => {
        updateBuilding()
    }

    createDetails = (objectToDisplay) => {
        return Object.keys(objectToDisplay).map( resource => {
            return (
                <div key={resource} className="Cost">
                    {resource.replace("_", " ") + ": " + objectToDisplay[resource]}
                </div>
            )
        })
    }

    showDetails = (event) => {     
        this.setState({details:(
            <div className="BuildingDetails" style={{left: this.refs.offsetLeft + 15, top: this.refs.offsetTop + 30, position: "absolute"}}>
                <div className='TechTitle'>Cost</div>
                <div className='TechCost TechPrint'>
                    {this.createDetails(Buildings[this.props.building].cost)}
                </div>
                <hr/>
                <div className='TechTitle'>Unlocks</div>
                <div className='TechAwards TechPrint' >
                    {this.createDetails(Buildings[this.props.building].awards)}
                </div>
            </div>
        )})
    }

    hideDetails = () => {
        this.setState({details: ""})
    }
    
    render() {
        return (
            <div>
                <div 
                    className="Building Clickable" 
                    onClick={this.purchaseBuilding} 
                    onMouseEnter={this.showDetails} 
                    onMouseLeave={this.hideDetails}
                    ref={this.addRef}
                    >
                    {this.props.building}
                </div>
                <div>
                    {this.state.details}
                </div>
            </div>
        );
    }
}


